   

import BackendAxios from "../repo"

const state = {
    Userinfo: {},
    Lables: [],
}

const getters = {
    Userinfo_getters: (state) => state.Userinfo,
    Lables_getters: (state) => state.Lables,


}
const actions = {
    GET_USER_API({ commit }) {
        return new Promise((resolve, reject) => {
            BackendAxios.get('UserAPI/')
                .then(data => {
                    commit('ChangeUserinfo_mutation', data.data)
                    resolve(data)
                })
                .catch(err => {
                    reject(err)
                })
        })
    },
    GET_LABLES_API({ commit }) {
        return new Promise((resolve, reject) => {
            BackendAxios.get('LablesAPI/')
                .then(data => {
                    commit('Lables_mutation', data.data)
                    resolve(data)
                })
                .catch(err => {
                    reject(err)
                })
        })
    },
    GET_MESSAGE_API({ commit }) {
        commit
        return new Promise((resolve, reject) => {
            BackendAxios.get('MessageAPI/')
                .then(data => {
                    resolve(data)
                })
                .catch(err => {
                    reject(err)
                })
        })
    },
    
}

const mutations = {
    ChangeUserinfo_mutation(state, data) {
        state.Userinfo = data
    },
    Lables_mutation(state, data) {
        state.Lables = data
    }

}

export const user__store = {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
};